<template>
    <div>
        <ul class="photos">
            <li class="photo" v-for="a in album.gallery" :key="a.src">
                <a @click="goPhoto(a.src)">
                    <img class="clip" :src="a.src">
                </a>
            </li>
        </ul>
        <darkroom></darkroom>
    </div>
</template>

<script>
import Darkroom from '@/components/Darkroom'

export default {
    name: 'album',
    components: {Darkroom},
    computed: {
        album() {
            return this.$store.state.album
        },
    },
    mounted() {
        if (!this.$store.state.album) {
            this.findIt()
        }
    },
    methods: {
        findIt() {
            const albums = this.$store.state.albums
            albums.forEach((album) => {
                if (album.codeName === this.$route.params.codeName) {
                    this.$store.commit('nowPlayingAlbum', album)
                }
            }, this);
        },
        goPhoto(photo) {
            this.$store.commit('nowPlayingPhoto', photo)
        }
    }
}
</script>

<style>
.photos {
    list-style: none;
    padding: 0;
    margin: 0;
    overflow: hidden;
}

.photos .photo {
    overflow: hidden;
    margin: 20px;
}

.photos .photo .clip {
    display: block;
    width: 100%;
}
</style>



