<template>
    <a @click="dismiss">
        <div class="darkroom" v-if="photo">
            <img :src="photo" class="photo">
        </div>
    </a>
</template>

<script>
export default {
    name: 'darkroom',
    computed: {
        photo() {
            return this.$store.state.photo
        }
    },
    methods: {
        dismiss() {
            this.$store.state.photo = ''
        }
    }
}
</script>

<style>
.darkroom {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    z-index: 3;
}

.darkroom .photo {
    max-width: 100%;
    max-height: 100%;
    margin: auto;
}
</style>



