import Vue from 'vue'
import Router from 'vue-router'
import Cover from '@/components/Cover'
import Album from '@/components/Album'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Cover',
      component: Cover
    },
    {
      path: '/:codeName',
      name: 'Album',
      component: Album
    }
  ]
})



