<template>
  <ul class="albums">
    <li class="album" v-for="a in albums" :key="a.codeName">
      <a @click="goAlbum(a)">
        <img class="cover" :src="a.gallery[0].src" :style="{height: coverSize, width: coverSize}">
        <div class="title" :style="{lineHeight: coverSize}">{{a.name}}</div>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'cover',
  computed: {
    albums() {
      return this.$store.state.albums
    },
    coverSize() {
      return window.innerWidth - 40 + 'px'
    }
  },
  methods: {
    goAlbum(album){
      this.$router.push(album.codeName)
      this.$store.commit('nowPlayingAlbum', album)
    }
  },
  mounted(){
    this.$store.state.album = ''
  }
}
</script>

<style>
.albums {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.albums .album {
  overflow: hidden;
  position: relative;
  margin: 20px;
}

.albums .album .cover {
  display: block;
  object-fit: cover;
}

.albums .album .title {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 14px;
  text-shadow: 0 0 2px rgba(255, 255, 255, 0.8);
}
</style>



