import Vue from 'vue'
import Vuex from 'vuex'

import Gallery from '@/Gallery'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    albums: Gallery,
    album: '',
    photo: ''
  },
  mutations: {
    nowPlayingAlbum(state, album) {
      state.album = album
    },
    nowPlayingPhoto(state, photo) {
      state.photo = photo
    }
  }
})



