<template>
  <div class="stat-place">
    <div class="stat">{{title}}</div>
    <router-link to="/" class="back" v-if="title!=='Camera del Cavallo'">
      <div class="chevron"></div>
      <div class="logo"></div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'stat',
  computed: {
    title() {
      if (this.$store.state.album) {
        return this.$store.state.album.name
      } else {
        return 'Camera del Cavallo'
      }
    }
  }
}
</script>

<style>
.stat-place {
  height: 40px;
  width: 100%;
  position: fixed;
  top: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
  z-index: 2;
}

.stat {
  height: 20px;
  width: 100%;
  background-color: rgb(250, 250, 250);
}

.back {
  position: absolute;
  left: 0;
  top: 0;
  height: 40px;
  width: 40px;
}

.back .chevron {
  position: absolute;
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: rgb(60, 60, 60);
  height: 7px;
  width: 7px;
  top: 6px;
  left: 6px;
  transform: rotate(-135deg);
}

.back .logo {
  background-image: url("../assets/logo.svg");
  width: 14px;
  height: 14px;
  background-size: 14px 14px;
  background-repeat: no-repeat;
  position: absolute;
  left: 14px;
  top: 3px;
}
</style>



